<template>
	<div id="search" class="flex-page">
    <div class="page-header">
      <form action="/">
        <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          show-action
          @search="onSearch"
          @cancel="onCancel"
        />
      </form>
    </div>
    <div class="page-body">
      <van-list
        v-model="loading"
        :finished="finished"
        @load="scrollLoad"
        :offset="100"
      >
        <div class="list">
          <div v-for="(i,index) in lists" :key="index" @click="$router.push({path:'/goodsdetail', query: {coupon_links:i.coupon_link,
          commission_moneys:i.commission_money,
          images:i.thumb,
          yishou:i.sales_num,
          id: i.origin_id,
          delprice:i.origin_price,
          title:i.title,
          nowPrice:i.coupon_price,
          quanJine:i.coupon_money,
          thumbs:i.thumb}})" class="good-list">
            <img style="width: 140px;height: 160px" :src="i.thumb"/>
            <div>
              <span class="searchlist-goodsname" style="overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-line-clamp: 2; -webkit-box-orient: vertical;">{{i.sub_title}}</span>
              <div><span>￥{{i.coupon_price}}</span><span>原价 <del>￥{{i.origin_price}}</del></span></div>
              <div class="lq">
                <div><img src="static/img/lq.png"/><span>领券 {{i.coupon_money}}元</span></div>
                <span>已售{{i.sales_num > 10000 ? (i.sales_num /10000 + '万') : i.sales_num}}</span>
              </div>
              <div class="yg">
                <div>预估赚￥{{i.commission_money}}</div>
                <div>升级赚￥5.20</div>
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </div>
	</div>
</template>

<script>
    export default {
        name: "channelList",
        data() {
        	return {
            value: '',
            loading: false,
            finished: true,
            nowPage: 1,
            searchList: [],
            lists:[],
          }
        },
      created(){
        console.log(localStorage.getItem("nowValue"))
        if (localStorage.getItem("nowValue")==''||localStorage.getItem("nowValue")==null){
          return false;
        } else{
          this.getGoodsLists()
        }
      },
      methods: {
        onCancel () {
          this.$router.go(-1)
        },
        onSearch () {
          this.finished = false
          this.getGoodsList()
        },
        // 滚动加载
        scrollLoad () {
          this.loading=true;
          this.getGoodsList()
          this.nowPage++;
        },
        // 根据顶部分类关键字获取商品
        getGoodsList(){
          let param = {
            word: this.value,
            page: this.nowPage
          }
          this.$POST('/user/sssp',param,false).then(res=>{
            console.log(res)
            if(res.code === 200) {
              if(res.data.length > 0){
                this.lists = res.data
                localStorage.setItem("parameter",this.value)
                if(res.data.length < 20){
                  this.finished=true;
                  this.$toast("已经到底了！");
                }
                this.loading=false;
              }
            } else {
              this.loading=false;
              this.finished=true;
              this.$toast('没有找到相关数据')
            }
          }).catch(err=>{
            console.log(err)
          })
        },
        getGoodsLists(){
          let param = {
            word: localStorage.getItem("nowValue"),
            page: this.nowPage
          }
          this.$POST('/user/sssp',param,false).then(res=>{
            console.log(res)
            if(res.code === 200) {
              if(res.data.length > 0){
                this.lists = res.data
                localStorage.setItem("parameter",this.value)
                if(res.data.length < 20){
                  this.finished=true;
                  this.$toast("已经到底了！");
                }
                this.loading=false;
              }
            } else {
              this.loading=false;
              this.finished=true;
              this.$toast('没有找到相关数据')
            }
          }).catch(err=>{
            console.log(err)
          })
        },

      }
    }
</script>

<style scoped lang="less">
  .flex-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    .page-header {
      flex-grow: 0;
    }
    .page-body {
      flex-grow: 1;
      overflow-y: scroll;
      overflow-x: hidden;
    }
  }
  #search {
    .van-nav-bar__title {
      width: 80%;
    }
  }
  /*.searchlist-goodsname {*/
    /*overflow: hidden;*/
    /*text-overflow: ellipsis;*/
    /*display: -webkit-box;*/
    /*-webkit-line-clamp: 2;*/
    /*-webkit-box-orient: vertical;*/
  /*}*/
  .good-list{
    width: 95%;
    text-align: left;
    margin: 0 auto;
    margin-bottom: 10px;
  }
  .good-list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .list:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .good-list>img{
    float: left;
    width: 50%;
  }
  .good-list>div{
    width: 49%;
    float: left;
    text-align:left;
    margin-left:2%;
  }
  .good-list>div>span{
    font-family: 黑体;
    font-size: 18px;
    font-weight: bold;
  }
  .good-list>div>div>span:first-child {
    font-size: 20px;
    color:#e12532;
  }
  .good-list>div>div>span:last-child{
    font-size: 13px;
    float:right;
    line-height: 26px;
    color: #a8a9a9;
  }
  .good-list>div>div{
    margin-top: 15px;
  }
  .good-list>div>div:after{
    content:"";
    display:block;
    height:0;
    clear:both;
    visibility:hidden;
  }
  .lq{
    width: 100%;
  }
  .lq:after{
    clear:both;
    content:'';
    display:block;
    width:0;
    height:0;
    visibility:hidden;
  }
  .lq>div{
    width: 40%;
    position: relative;
  }
  .lq>div:first-child{
    float: left;
    height:30px;
  }
  .lq>div>img{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    display: block;
    width: 100%;
  }
  .lq>div>span{
    z-index: 1;
    line-height:25px;
    position: absolute;
    left: 18%;
    color: #ffffff;
    font-size: 14px;
  }
  .yg{
    width: 100%;
    font-size: 12px;
    display: flex;justify-content: space-around;
  }
  .yg>div:first-child{
    width: 5rem;
    white-space: nowrap;
    overflow:hidden;
    padding: 2px 5px;
    background: #ffdcdc;
    color: #e12532;
    border-radius:7px;
  }
  .yg>div:last-child{
    width: 5rem;
    white-space: nowrap;
    overflow:hidden;
    padding: 2px 5px;
    background: linear-gradient(to right,#ff722d, #ff4b26 , #ff2820); /* 标准的语法 */
    border-radius:7px;
    color: #ffffff;
  }
</style>



